.App {
  font-family: sans-serif;
  text-align: center;
  font-size: 12px;
}

table {
  border: 1px solid #dee2e6;
  border-collapse: collapse;
  width: 100%;
}

table th {
  padding: 10px 0;
  background: #dee2e6;
}

table td {
  border-right: 1px solid #dee2e6;
  border-left: 1px solid #dee2e6;
  border-bottom: 1px solid #dee2e6;
}
table .day .header {
  text-align: center;
  padding: 10px 0;
  font-size: 11px;
  border-bottom: 1px solid #dee2e6;
  /* background: #dee2e6; */
}
table .day .icon {
  padding: 15px 5px 0;
  opacity: 0;
}
table .day .icon img {
  width: 20px;
}

table .day .people {
  display: block;
  padding: 10px 10px;
  min-height: 14px;
  /* font-style: italic; */
}

table td.active {
  background: #dee2e6;
  font-weight: bold;
}

table td.active .icon {
  opacity: 1;
}

table td.past {
  opacity: 0.2;
}
